.search-form {
  height: 60px;
  background: radial-gradient(circle, #8094f6 0%, #e27dff 100%);
  justify-content: center;
  gap: 10px;
  display: flex;
}

input {
  height: 55px;
  width: 250px;
  border: none;
  border-radius: 4px;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: calc(50% - 7.5px);
}

button {
  cursor: pointer;
  color: #fff;
  text-align: center;
  min-width: 169px;
  height: 56px;
  background-color: #3b428a;
  border: none;
  border-radius: 4px;
  align-items: center;
  padding: 16px 32px;
  font-size: 16px;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

